import {Component} from '@angular/core';
import {IonicPage, NavController, NavParams} from 'ionic-angular';
import {CellModel} from "../../models/cell-model";
import {CellProvider} from "../../providers/cellgroups/cell-provider";
import {FunctionsProvider} from "../../providers/functions/functions";
import {AngularFireAuth} from 'angularfire2/auth';

@IonicPage()
@Component({
  selector: 'page-chat',
  templateUrl: 'cellgroups.html',
})
export class Cellgroups {


  cellgroups: CellModel= [];
  private obj: any;
  private bId: any = "1";
  private bid:  any = "1";


  constructor(navParams: NavParams,
              private cellgroup: CellProvider,

              public navCtrl: NavController,
              private func: FunctionsProvider,
              private  afs: AngularFireAuth,


  ){
this.checkAuth()


    this.obj= navParams.get('data') || "None";
   // this.bId = "1";

this.loadData()

  }
checkAuth(){
    this.afs.authState.subscribe(res => {
        if (res && res.uid) {

            this.func.sync(res.uid,this.bid)

        } else {

            this.navCtrl.setRoot('login',{
                refresh:"true",
            });
        }
    });
}

    async loadData(){

        this.cellgroup.query().then((res: any) => {

            this.cellgroups = JSON.parse(res.data)




        }).catch(er=> console.log(er))
        ;
    }



openCellDetail(obj){

console.log(this.obj);
  this.navCtrl.push('CellGroupDetailPage', {

    data: obj

  });

}



}



